*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  font-family: "Lato", sans-serif;
}

.App {
  font-family: sans-serif;
  text-align: center;
  max-width: 850px;
  margin: 40px auto;
}

h1 {
  text-align: center;
}

.home-page {
  background-color: rgb(219, 238, 239);
  padding: 20px;
  display: flex;
  align-items: center;
}

.finn-story {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;
  max-width: 750px;
  min-width: 400px;
  margin: auto;
  text-align: center;
}

.gallery {
  display: flex;
  flex-direction: column;
  text-align: center;
  justify-content: center;
  align-items: center;
  max-width: 750px;
  min-width: 400px;
  margin: auto;
  text-align: center;
}

.standard-img {
  max-width: 90%;
  max-height: 700px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.multi-img {
  max-width: 50%;
}

.multi-img-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 750px;
  margin-left: auto;
  margin-right: auto;
}

.navbar {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  height: 60px;
  width: 100%;
  background-color: black;
  margin: 0px auto;
  padding: 0px;
}
.navbar li {
  list-style: none;
  text-align: center;
  margin: auto;
}
.navbar li a {
  text-decoration: none;
  color: whitesmoke;
  font-size: 18px;
  font-family: Arial, Helvetica, sans-serif;
  padding: 8px 20px;
}
.navbar li a:hover {
  color: gold;
  border: white;
}
.navbar li a:active {
  background-color: darkslategrey;
}

.header-img {
  display: block;
  max-width: 6%;
}

.header-text {
  font-family: cursive;
  font-size: 16pt;
}

.header {
  background-color: cornsilk;
  border-radius: 0px;
  margin: 0px auto;
  padding: 25px 40px;
  display: flex;
  flex-direction: row;
  gap: 30px;
}

.btn {
  background-color: lightcoral;
  font-size: medium;
  border-radius: 8px;
  padding: 10px 20px;
  border: 4px solid white;
}
